@charset "utf-8";
*{margin: 0; padding: 0;}
li{list-style-type: none;}
a{text-decoration: none; color: inherit;}
.inner{width: 1200px; margin: 0 auto;}
body{font: 12px; color: #333; 
  font-family: "Noto Sans KR", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

